import {
  setProvider,
  web3,
  Program,
  AnchorProvider
} from '@project-serum/anchor'
import { AnchorWallet } from '@solana/wallet-adapter-react'

import idl from './otter_cash_idl.json'

export const LEVELS = 20
export const OTTER_PROGRAM_ID = new web3.PublicKey(
  'otterXYtgZ5DRUGX6JGtcZPg3GoWxEqcLrb9MjeCv3X'
)
export const OTTER_MERKLE_STATE_ID = new web3.PublicKey(
  '3dyQbbRrkPtbjsP6pcJL5eWA5TS3BkjY6xS73UTaBsD1'
)

export const NETWORK = 'mainnet'
console.log('NETWORK:', NETWORK)
let NETWORK_URL: string
if (NETWORK === 'mainnet') {
  NETWORK_URL = 'https://white-quiet-glitter.solana-mainnet.quiknode.pro/2d4bcbb69148d23b481215ff6c3776ecb183f698/'
} else if (NETWORK === 'devnet') {
  NETWORK_URL = 'https://nameless-icy-violet.solana-devnet.quiknode.pro/90ccb55668a25df17eaa22f5eda5897657dbc72e/'
} else {
  throw new Error('Unreachable.')
}

export const connection = new web3.Connection(
  NETWORK_URL,
  {
    commitment: 'processed',
    confirmTransactionInitialTimeout: 130000,
    disableRetryOnRateLimit: false
  }
)

setProvider(new AnchorProvider(
  connection,
  null,
  {
    commitment: 'processed',
    preflightCommitment: 'processed',
    maxRetries: 32
  }
))
export let program = new Program(idl, OTTER_PROGRAM_ID)

export function setAnchorProvider (wallet: AnchorWallet) {
  setProvider(new AnchorProvider(
    connection,
    wallet,
    {
      commitment: 'processed',
      preflightCommitment: 'processed',
      maxRetries: 32
    }
  ))
  program = new Program(idl, OTTER_PROGRAM_ID)
}

export const sleep = ms => new Promise((resolve, reject) => setTimeout(resolve, ms))

export async function errorToast (
  toast: () => void,
  title: string,
  fn: () => Promise<void>
): Promise<void> {
  try {
    await fn()
  } catch (e) {
    let description = e.message
    if (description.includes('custom program error: 0x1773')) {
      description = 'Someone else deposited while this deposit was processing. Please try again.'
    } else if (description.includes('Constraint doesn\'t match main.tree')) {
      description = 'Invalid input was provided to the zero-knowledge proof generator. Please check that your note was copied correctly.'
    }
    toast({
      title: title,
      description: description,
      status: 'error',
      isClosable: true,
      position: 'bottom-left',
      duration: null
    })
    throw e
  }
}
