import React from 'react'
import mixpanel from 'mixpanel-browser'
import { useNavigate, useLocation } from 'react-router-dom'
import { WalletMultiButton, useWalletModal } from '@solana/wallet-adapter-react-ui'

import { useAnchorWallet } from '@solana/wallet-adapter-react'

import { setAnchorProvider } from './ContractCommon'

import {
  useBreakpointValue,
  Box,
  Button,
  Center,
  Flex,
  Image,
  Link,
  Spacer
} from '@chakra-ui/react'

import { IconContext } from 'react-icons'
import { FiTwitter, FiGithub, FiMail } from 'react-icons/fi'

import otterLogoFull from './icons/otterlogo_full.svg'
import otterLogoGlyph from './icons/otterlogo_glyph.svg'
import footerIcon from './icons/footer.svg'

type CurrentPage = 'homepage' | 'deposit' | 'withdraw' | 'darkpool' | 'tutorial'

interface HeaderProps {
  currentPage: CurrentPage
}
function Header (props: HeaderProps): React.ReactElement {
  const navigate = useNavigate()
  return (
    <Flex ml={['6%', '3%']} mr="5%">
      <Box
        w={['28%', '18%']}
        m={['6% 0 6% 0', '1.5% 0 0.5% 0']}
        onClick={() => {
          mixpanel.track('Otter Logo - Button Click')
          navigate('/')
        }}
        sx={{
          cursor: 'pointer'
        }}
      >
        <Image src={useBreakpointValue({ base: otterLogoGlyph, sm: otterLogoFull })} />
      </Box>
      <Spacer />
      <Button
        variant="plain"
        fontWeight={['600', '700']}
        fontSize={['1em', '1.2em']}
        display="none"
        onClick={() => {
          mixpanel.track('Tutorial - Button Click')
          navigate('/tutorial')
        }}
      >
        Tutorial
      </Button>
      <Button
        variant="fancy"
        padding={['0.9em', '1.3em']}
        fontSize={['0.9em', '1.1em']}
        border={['1px', '2px']}
        display={
          props.currentPage === 'deposit' || props.currentPage === 'withdraw' ? 'none' : 'inherit'
        }
        onClick={() => {
          mixpanel.track('Launch App - Button Click')
          navigate('/deposit')
        }}
      >
        Launch App
      </Button>
      <Box
        display={
          props.currentPage === 'deposit' || props.currentPage === 'withdraw' ? 'inherit' : 'none'
        }
      >
        <WalletMultiButton />
      </Box>
    </Flex>
  )
}

function Footer (): React.ReactElement {
  return (
    <Flex
      position="absolute"
      bottom="0"
      w="100%"
      h="15%"
    >
      <Box
        position="absolute"
        bottom="110px"
        h="3px"
        w="85%"
        borderRadius="2px"
        bg="black"
      />
      <Image
        position="absolute"
        bottom="80px"
        w="calc(100px + 1%)"
        left="calc(10px + 10%)"
        src={footerIcon}
      />
      <Flex
        position="absolute"
        bottom="55px"
        right="calc(25px + 11%)"
        flexWrap="nowrap"
      >
        <IconContext.Provider
          value={{
            size: useBreakpointValue({ base: '35', sm: '40' }),
            style: { strokeWidth: useBreakpointValue({ base: '1.25px', sm: '1.6px' }) }
          }}
        >
          <Link href="https://twitter.com/OtterCashHQ" isExternal mr="6%">
            <FiTwitter />
          </Link>
          <Link href="https://github.com/otter-cash" isExternal mr="6%">
            <FiGithub />
          </Link>
          <Link href="mailto:chris@otter.cash" isExternal>
            <FiMail />
          </Link>
        </IconContext.Provider>
      </Flex>
    </Flex>
  )
}

interface HeaderFooterWrapperProps {
  children: React.ReactNode
  currentPage: CurrentPage
}
export function HeaderFooterWrapper (props: HeaderFooterWrapperProps): React.ReactElement {
  return (
    <Box
      w="100vw"
      maxW="100%"
      minH="100vh"
      pb="calc(280px + 4%)"
    >
      <Header currentPage={props.currentPage} />
      <Center>
        {props.children}
      </Center>
      <Footer />
    </Box>
  )
}

interface DepositWithdrawButtonProps {
  isDeposit: boolean
  isManuallyPaused: boolean
  disableClick: boolean
  onClick: any
  text: string
  style: {}
}
export function DepositWithdrawButton (props: DepositWithdrawButtonProps) {
  const wallet = useAnchorWallet()
  const { setVisible } = useWalletModal()
  let text = props.text
  if (props.isManuallyPaused) {
    text = 'Temporarily Paused'
  } else if (props.isDeposit && !wallet) {
    text = 'Connect Wallet'
  } else if (props.isDeposit) {
    setAnchorProvider(wallet)
  }
  if (props.disableClick || props.isManuallyPaused) {
    props.style.opacity = '40%'
  }
  function onClick (): void {
    if (props.isDeposit) {
      if (!wallet) {
        setVisible(true)
        return
      }
      setAnchorProvider(wallet)
    }
    if (props.disableClick || props.isManuallyPaused) {
      return
    }
    props.onClick()
  }
  return (
    <Button
      variant="fancy"
      padding="1em"
      fontFamily="Montserrat"
      border={['1px', '2px']}
      sx={props.style}
      onClick={onClick}
    >
      {text}
    </Button>
  )
}

export function updateIntercomDisplay () {
  const location = useLocation()
  const isMobile = useBreakpointValue({ base: true, sm: false })
  const shouldShow = ['/deposit', '/withdraw'].includes(location.pathname) && !isMobile
  window.Intercom('update', { hide_default_launcher: !shouldShow })
}
